import { Component, OnInit } from '@angular/core';
import { InicioService } from '../../../providers/Almacenamiento/inicio.service';
import { ActivatedRoute } from '@angular/router';
import { InventariosService } from '../../../providers/Api/Inventarios.service';

@Component({
  selector: 'app-ivt-miinventario',
  templateUrl: './ivt-miinventario.page.html',
  styleUrls: ['./ivt-miinventario.page.scss'],
})
export class IvtMiinventarioPage implements OnInit {

  id = '';
  ver = false;

  creador = '';
  constructor(
    private almacenamiento: InicioService,
    private active: ActivatedRoute,
    private ivtApi: InventariosService
  ) { }

  ngOnInit() {
  }

  ionViewWillEnter() {
    this.id = this.active.snapshot.paramMap.get('id');
    this.iniciar();
  }

  async iniciar() {
      const info = await this.almacenamiento.obtenerToken();
      
      if ( info ) {
          this.ivtApi.leerInventario(info.token, this.id).subscribe((data: any) => {
            if (data.exe) {
              this.creador = data.response.creador;
              if (data.response.creador !== info._id) {
                this.ver = false;
              } else {
                this.ver = true;
              }
            }
          })
      }
  }

}
